.button.is-primary {
  background: #fff;
  box-shadow: 6px 6px rgba(255, 255, 255, 0.65);
  color: #151515;

  padding: 1rem 2rem;
  display: inline-flex;
  position: relative;
  z-index: 1;
  transition: all 0.3s cubic-bezier(0.785, 0.135, 0.15, 0.86);
  /* Apply transition to all properties */
  cursor: pointer;
  box-sizing: border-box; /* Ensure padding and border don't affect the size */
}
.button.is-primary:hover {
  box-shadow: 0px 0px rgba(255, 255, 255, 0.65);
  transform: translateY(3px);
  transform: translateX(3px);
  color: #212529;
}

.button.is-secondary {
  background: #0c0d0d;
  border: 1px solid #212121;
  box-shadow: 6px 6px #212121; /* Offset by 5px and 1px blur */

  padding: 1rem 2rem;
  display: inline-flex;
  justify-content: center;
  position: relative;
  z-index: 1;
  transition: all 0.3s cubic-bezier(0.785, 0.135, 0.15, 0.86);
  /* Apply transition to all properties */
  cursor: pointer;
  box-sizing: border-box; /* Ensure padding and border don't affect the size */
}

.button.is-secondary:hover {
  box-shadow: 0px 0px #151515;
  transform: translateY(3px);
  transform: translateX(3px);
}

