/* ================== */
/* ? HERO SECTION */
/* ================== */

.container-large.projects {
  width: 100%;
  margin: 0 auto;
  font-family: sans-serif;

  display: grid;

  grid-template-rows:
    100px
    auto
    max-content
    150px;
  grid-template-columns: repeat(12, 1fr);

  /* prettier-ignore */
  grid-template-rows:
  max-content 
  max-content
  max-content
  [lines-start] max-content [lines-end];

  grid-template-columns: repeat(12, 1fr);

  column-gap: 24px;
}

.project-1--part-1-1 {
  z-index: 99;
  grid-row: 1 / 2;
  grid-column: 3 / 7;
}

.project-1--part-1-2 {
