/* ================== */
/* ? LINE SEPARATOR */
/* ==================  */

.benefits-container {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: minmax(min-content, 1fr) auto minmax(min-content, 1fr);
  gap: 0;
  justify-content: center;
  overflow: hidden;
}

.benefit_wrapper.is-track-top {
  grid-row: 1 / 2;
  grid-column: 1 / -1;
}

