/* ================== */
/* ? PLAN-GRID */
/* ==================  */

.section_plan-v2 {
  grid-template-columns: 6.75rem 1fr minmax(auto, max-content) 1fr 6.75rem;
  grid-template-rows: min-content min-content;
  justify-content: center;
}

/* Lines */
.plan-lines_center-line {
  width: clamp(1rem, 100%, 5rem);
}

.header_component-v2 {
  grid-column: 3/4;
  grid-row: 1/2;
}

.plan-container-v2 {
  grid-column: 3/4;
  grid-row: 2/3;
}

.plan_grid-v2 {
  inset: unset;
}

.plan_grid-v2.is-left {
  width: 100%;
  grid-column: 1/3;
  grid-row: 2/3;
  position: relative;
  align-self: center;
}

.plan_grid-v2.is-right {
  width: 100%;
  grid-column: 4/6;
  grid-row: 2/3;
  position: relative;
  align-self: center;
}

.plan_grid-v2.is-left::before,
.plan_grid-v2.is-right::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 9;
  background-color: #0c0d0d;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  pointer-events: none;
  mask-size: cover;
}

.plan_grid-v2.is-left::before {
  -webkit-mask-image: radial-gradient(
    ellipse farthest-corner at left,
    rgba(0, 0, 0, 0) -30%,
    rgba(12, 13, 13, 1) 60%
  );
  mask-image: radial-gradient(
    ellipse farthest-corner at left,
    rgba(0, 0, 0, 0) 0%,
    rgba(12, 13, 13, 1) 60%,
    rgba(12, 13, 13, 1) 100%
  );
}

.plan_grid-v2.is-right::before {
  -webkit-mask-image: radial-gradient(
    ellipse farthest-corner at right,
    rgba(0, 0, 0, 0) -30%,
    rgba(12, 13, 13, 1) 60%
  );
  mask-image: radial-gradient(
    ellipse farthest-corner at right,
    rgba(0, 0, 0, 0) 00%,
    rgba(12, 13, 13, 1) 60%,
    rgba(12, 13, 13, 1) 100%
  );
}

