/* ================== */
/* ? FORM */
/* ================== */

.form_component {
  position: relative;
  border: 1px solid transparent;
  border-bottom: none;
  border-radius: 16px;

  /* prettier-ignore */
  background: linear-gradient(
    to bottom,
    rgba(16, 17, 17, 1) 0%,
    rgba(12, 13, 13, 0) 100%
  );
}

.form_component::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;

  border: inherit;
  border-bottom: inherit;
  border-radius: inherit;

  /* prettier-ignore */
  background: linear-gradient(
    to bottom,
    rgba(70, 70, 70, 1) 0%,
    rgba(70, 70, 70, 0) 100%
  )
    border-box;
  mask:
    linear-gradient(white, white) padding-box,
    linear-gradient(white, white);
  mask-composite: exclude;

  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  pointer-events: none;
}

/* ================== */
/* ? NORMAL INPUT */
/* ================== */

.form-input {
  transition:
    border 0.3s ease,
    box-shadow 0.2s ease;
}

.form-input:focus {
  background-color: #0c0d0d;

  box-shadow:
    0 0 3px rgba(114, 114, 114, 0.2),
    0 0 6px rgba(114, 114, 114, 0.2),
    0 0 9px rgba(114, 114, 114, 0.2);
}

/* ================== */
/* ? RADIO */
/* ================== */

.form-input {
  background-color: #0c0d0d;
}

.form-radio {
  cursor: pointer;
}

.form-radio:hover,
.form-radio:hover > .form-radio-icon {
  border-color: #727272; /* Change to the desired color */
}

/* ================== */
/* ? TEXT AREA */
/* ================== */

.form-input.is-text-area {
  resize: none;
  position: relative;
  overflow: hidden;
}

.form-input.is-text-area::-webkit-resizer {
  display: none;
}

/* Auto-fill styles */

/* Style for WebKit browsers (Chrome, Safari) */
.form-input:-webkit-autofill,
.form-input:-webkit-autofill:focus {
  transition:
    background-color 0s 600000s,
    color 0s 600000s,
    border 0.3s ease,
    box-shadow 0.3s ease !important;
}

/* Additional styles for other browsers */

.form-input:-moz-autofill,
.form-input:-moz-autofill:focus {
  transition:
    background-color 0s 600000s,
    color 0s 600000s,
    border 0.3s ease,
    box-shadow 0.3s ease !important;
}

/* ================== */
/* ? ACCORDION */
/* ================== */

.open {
  border: 1px solid #727272;
}
.accordion-item {
  grid-row-gap: 0;
}

.accordion-item-header {
  cursor: pointer;
}

.accordion-item-header-icon {
  transition: transform 0.2s ease;
}

.accordion-item.open .accordion-item-header-icon {
  transform: rotate(135deg);
}

.accordion-item-description-wrapper {
  display: grid;
  grid-template-rows: 0fr;
  overflow: hidden;
  transition: grid-template-rows 0.2s ease;
}

.accordion-item.open .accordion-item-description-wrapper {
  grid-template-rows: 1fr;
}

.accordion-item-description {
  min-height: 0;
}

/* ================== */
/* ? CTA */
/* ================== */

.container-large.radial-gradient,
.container-large.cta_radial-gradient,
.container-large.radial-gradient-projects {
  position: relative;
}

.container-large.radial-gradient::before,
.container-large.cta_radial-gradient::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 999;
  background-color: #0c0d0d;
  -webkit-mask-image: radial-gradient(
      circle 100px at var(--mouse-x, 50%) var(--mouse-y, 50%),
      transparent 0,
      #000 100%
    ),
    radial-gradient(
      ellipse at center,
      rgba(0, 0, 0, 0) 0%,
      rgba(12, 13, 13, 1) 80%,
      rgba(12, 13, 13, 1) 100%
    );
  -webkit-mask-composite: source-in;
  mask-image: radial-gradient(
      circle 100px at var(--mouse-x, 50%) var(--mouse-y, 50%),
      transparent 0,
      #000 100%
    ),
    radial-gradient(
      ellipse at center,
      rgba(0, 0, 0, 0) 0%,
      rgba(12, 13, 13, 1) 80%,
      rgba(12, 13, 13, 1) 100%
    );
  mask-composite: intersect;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  pointer-events: none;
  mask-size: cover, cover;
}

.container-large.radial-gradient-projects::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 999;
  background-color: #0c0d0d;
  -webkit-mask-image: radial-gradient(
      circle 100px at var(--mouse-x, 50%) var(--mouse-y, 50%),
      transparent 0,
      #000 100%
    ),
    radial-gradient(
      ellipse at center,
      rgba(0, 0, 0, 0) 0%,
      rgba(12, 13, 13, 1) 80%,
      rgba(12, 13, 13, 1) 100%
    );
  -webkit-mask-composite: source-in;
  mask-image: radial-gradient(
      circle 100px at var(--mouse-x, 50%) var(--mouse-y, 50%),
      transparent 0,
      #000 100%
    ),
    radial-gradient(
      ellipse at center,
      rgba(0, 0, 0, 0) -50%,
      rgba(12, 13, 13, 1) 80%,
      rgba(12, 13, 13, 1) 100%
    );
  mask-composite: intersect;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  pointer-events: none;
  mask-size: cover, cover;
}

